.landing-splash-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 100px; }
  @media screen and (min-width: 992px) {
    .landing-splash-container {
      margin-top: 0;
      margin-bottom: 0;
      width: 59%; }
      .landing-splash-container .slick-dots {
        transform: translateX(60px); } }
  @media screen and (min-width: 1200px) {
    .landing-splash-container {
      width: 66%; } }
  @media screen and (min-width: 1600px) {
    .landing-splash-container {
      width: 63%; } }
  .landing-splash-container .slick-slider {
    width: 100%; }
  .landing-splash-container .landing-splash-slide-container {
    padding: 0 15px 30px calc(100% - 900px); }
    @media screen and (max-width: 991px) {
      .landing-splash-container .landing-splash-slide-container {
        padding: 0 15px; } }
    .landing-splash-container .landing-splash-slide-container .landing-splash-slide-inner {
      min-height: 436px;
      display: flex;
      align-items: center; }
    .landing-splash-container .landing-splash-slide-container .desc-container {
      display: flex;
      flex-direction: column;
      justify-content: center; }
      @media screen and (max-width: 991px) {
        .landing-splash-container .landing-splash-slide-container .desc-container {
          align-items: center;
          text-align: center;
          padding: 0 15px 15px 15px;
          max-width: 400px;
          margin: auto; } }
      .landing-splash-container .landing-splash-slide-container .desc-container h2 {
        font-weight: 800;
        font-size: 36px;
        white-space: pre-wrap; }
        @media screen and (max-width: 991px) {
