            font-size: 24px; } }
      .landing-splash-container .landing-splash-slide-container .desc-container p {
        font-size: 18px; }
        @media screen and (max-width: 991px) {
          .landing-splash-container .landing-splash-slide-container .desc-container p {
            font-size: 16px; } }
    @media screen and (max-width: 991px) {
      .landing-splash-container .landing-splash-slide-container .splash-img-container {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 15px; } }
    .landing-splash-container .landing-splash-slide-container .splash-img-container .splash-img {
      max-width: 400px;
      transition: transform 0.3s ease;
      transform: scale(0.95); }
      @media screen and (max-width: 991px) {
