          max-width: 300px; } }
      .landing-splash-container .landing-splash-slide-container .splash-img-container .splash-img img {
        width: 100%;
        height: auto; }
      .landing-splash-container .landing-splash-slide-container .splash-img-container .splash-img:hover {
        transform: scale(1); }
      .landing-splash-container .landing-splash-slide-container .splash-img-container .splash-img:active {
        transform: scale(0.95); }
  .landing-splash-container .type-2 .splash-img img {
    max-width: 250px; }
  .landing-splash-container .type-2 .button-container .button-col:nth-of-type(1) {
    padding-right: 5px;
    margin-bottom: 10px; }
  .landing-splash-container .type-2 .button-container .button-col:nth-of-type(2) {
    padding-left: 5px;
    margin-bottom: 10px; }
  @media screen and (min-width: 991px) {
    .landing-splash-container .type-2 .splash-img {
      position: absolute;
      right: 15px;
      top: 0; } }
  @media screen and (min-width: 991px) and (min-width: 1200px) {
    .landing-splash-container .type-2 .splash-img img {
      max-width: 350px; } }
  @media screen and (min-width: 991px) {
    .landing-splash-container .type-2 .desc-container {
      min-height: 396px;
      justify-content: flex-end; }
      .landing-splash-container .type-2 .desc-container p {
        max-width: 400px; }
      .landing-splash-container .type-2 .desc-container .button-container {
        margin-right: auto; } }
  .landing-splash-container .type-3 h1 {
    font-family: "After-Night", sans-serif;
    font-size: 30.8px;
    transform: translateX(68px) translateY(-41px); }
    @media screen and (max-width: 991px) {
